import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
import { Link } from 'gatsby'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../styles/utils'
import { getLinkPath, imageSources } from '../utils'

import ProductThumbnail from './ProductThumbnail'

const StyledBestSellersSection = styled.section`
  margin: 20px 0 30.5px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin: ${desktopVw(80)} 0 ${desktopVw(76.6)};
    padding-right: ${desktopVw(80)};
  }
`

const FeaturedImage = styled.div`
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  box-sizing: border-box;
  margin: var(--marginMobile);
  padding: var(--paddingMobile);
  height: var(--heightMobile);
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: sticky;
    top: ${props => desktopVw(props.theme.header.desktop.height)};
    flex-shrink: 0;
    width: ${desktopVw(521)};
    height: calc(
      100vh - ${props => desktopVw(props.theme.header.desktop.height)}
    );
    margin: 0 ${desktopVw(79)} 0 0;
    padding: ${desktopVw(100)} ${desktopVw(80)};
  }
`

const StyledImage = styled(Image)`
  display: var(--displayMobile);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const TextWrapper = styled.div`
  h1,
  h2,
  h3,
  p {
    font-size: 30px;
    line-height: 30px;
    white-space: pre-wrap;
    text-transform: uppercase;
    color: var(--color);
    text-align: var(--textAlign);
  }

  i {
    font-family: ${props => props.theme.fonts.secondary};
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
  }

  z-index: 1;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    h1,
    h2,
    h3,
    p {
      font-size: ${desktopVw(30)};
      line-height: ${desktopVw(30)};
      text-align: initial;
    }

    i {
      font-size: ${desktopVw(35)};
    }
  }
`

const BestSellers = styled.ul`
  display: flex;
  flex: 1;
  margin: 10px 0 0;
  padding: 0 10px;
  overflow-x: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: column;
    flex-flow: wrap;
    align-items: flex-start;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
`

const BestSellerItem = styled.li`
  padding-right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(50.58)};
    padding: 0;
    width: calc(50% - ${desktopVw(20)});

    &:nth-last-child(-n + 2) {
      margin-bottom: 0;
    }
  }
`

const BestSellersSection = ({
  image,
  imageDesktop,
  json,
  textColor,
  products,
  hideImageOnMobile,
  tracker,
  link,
}) => {
  const imageRender = () => (
    <StyledImage
      fluid={imageSources(image, imageDesktop)}
      style={{
        position: 'absolute',
        left: '0',
        right: '0',
        bottom: '0',
        top: '0',
        zIndex: '0',
        '--displayMobile': hideImageOnMobile ? 'none' : 'block',
      }}
      alt={image.description || image.title}
    />
  )

  return (
    <StyledBestSellersSection>
      <FeaturedImage
        style={{
          '--marginMobile': hideImageOnMobile ? '0 0 26px' : null,
          '--paddingMobile': hideImageOnMobile ? null : ' 17px 20px',
          '--heightMobile': hideImageOnMobile ? null : '568px',
        }}
      >
        {link ? (
          <Link to={getLinkPath(link)}>{imageRender()}</Link>
        ) : (
          imageRender()
        )}
        <TextWrapper
          style={{
            '--textAlign': hideImageOnMobile ? 'center' : null,
            '--color': textColor.toLowerCase(),
          }}
        >
          {documentToReactComponents(json)}
        </TextWrapper>
      </FeaturedImage>
      <BestSellers>
        {products.map((product, index) => (
          <BestSellerItem key={product.id}>
            <ProductThumbnail
              contentfulProduct={product}
              tracker={
                tracker ? `${tracker}_you_may_also_like__${index}` : undefined
              }
              prefixName='You May Also Like'
            />
          </BestSellerItem>
        ))}
      </BestSellers>
    </StyledBestSellersSection>
  )
}

export default BestSellersSection
