import React from 'react'
import styled from 'styled-components'
import Instagram from '../icons/Instagram'
import { desktopVw } from '../../styles/utils'
import useDetectDevices from '../../hooks/useDetectDevices'

const IgHandle = styled.div`
  font-size: 11.5px;
  color: ${({ theme }) => theme.colors.darkGrey};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    transition: 0.5s ease;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: ${desktopVw(16)};
    color: ${({ theme }) => theme.colors.primary};
  }
`

export function InstagramHandle({
  instagramHandle,
  className,
  overrideAnchor = false,
}) {
  const { devices } = useDetectDevices()
  const anchorProps = overrideAnchor
    ? {}
    : {
        href: `https://www.instagram.com/${instagramHandle}/`,
        target: '_blank',
        rel: 'noopener noreferrer',
      }
  return (
    <IgHandle className={className}>
      <a {...anchorProps}>
        <div style={{ textAlign: 'center' }}>
          {devices.desktop && <Instagram />}
          <p>@{instagramHandle}</p>
        </div>
      </a>
    </IgHandle>
  )
}
