import React from 'react'
import useDetectDevices from '../../../hooks/useDetectDevices'
import Carousel from './index'

// TODO: move this logic into Carousel.js
export const CarouselWrapper = ({
  className,
  children,
  mobileSettings,
  desktopSettings,
}) => {
  const { devices } = useDetectDevices()

  return (
    <div className={className}>
      {devices.desktop && <Carousel {...desktopSettings}>{children}</Carousel>}
      {devices.mobile && <Carousel {...mobileSettings}>{children}</Carousel>}
    </div>
  )
}
