import React from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'

import { desktopVw } from '../styles/utils'

import Modal from './Modal'
import Rating from './Rating'
import Button from './Button'
import TextButton from './TextButton'
import CloseBtnIcon from '../assets/close-button.svg'

const StyledReviewModal = styled(Modal)`
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: scroll;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    height: auto;
  }
`

const Img = styled.img`
  max-height: 476px;
  width: 100%;
  object-fit: cover;
  background: ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    object-fit: contain;
    width: auto;
    max-height: 80vh;
    max-width: 60vw;
  }
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  padding: 20px 20px 29px;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: auto;
    width: ${desktopVw(480)};
    padding: ${desktopVw(75)} ${desktopVw(80)} ${desktopVw(52)};
  }
`

const Header = styled.header`
  margin-bottom: 13px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(16)};
  }
`

const RatingWrapper = styled.div`
  display: flex;
  align-items: center;
  font-size: 10px;
  text-transform: uppercase;
  color: ${props => props.theme.colors.greyLight};
  margin-bottom: 16px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(8)};
    font-size: ${desktopVw(10)};
  }
`

const StyledRating = styled(Rating)`
  margin-right: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(16)};
  }
`

const Author = styled.span`
  margin-right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(10)};
  }
`

const Time = styled.time``

const Title = styled.h1`
  font-size: 25px;
  line-height: 30px;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(25)};
    line-height: ${desktopVw(30)};
  }
`

const MessageContainer = styled.div`
  flex: 1;
  margin-bottom: 37px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(24)};
  }
`

const Message = styled.p`
  display: -webkit-box;

  overflow: hidden;
  width: 100%;
  margin-bottom: 13px;
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 24px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
    line-height: ${desktopVw(20)};
    margin-bottom: ${desktopVw(24)};
  }
`
const Footer = styled.footer`
  display: flex;
  justify-content: space-between;
`

const StyledButton = styled(Button)`
  text-transform: uppercase;
  padding: 18px 20px;
  font-size: 11px;
  font-family: ${props => props.theme.fonts.heading};
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(18)};
    font-size: ${desktopVw(11)};
  }
`

const StyledTextButton = styled(TextButton)`
  font-family: ${props => props.theme.fonts.heading};
  text-transform: uppercase;
  font-weight: 500;
  height: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const CloseBtnMobile = styled.button`
  position: absolute;
  top: 26px;
  right: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const ReviewModal = ({
  open,
  closeModal,
  activeModal,
  body: reviewMessage,
  onNextClick,
  title: reviewTitle,
  reviewsLength,
  dateCreated: reviewDate,
  author,
  onPrevClick,
  reviewerPhotoUrl,
  rating: reviewRating,
}) => {
  const formattedDate = dayjs(reviewDate).format('D MMM YYYY')

  return (
    <StyledReviewModal open={open} onClose={closeModal} showCloseBtn={false}>
      <CloseBtnMobile aria-label='Close' onClick={closeModal}>
        <CloseBtnIcon />
      </CloseBtnMobile>
      <Img loading='lazy' src={reviewerPhotoUrl} />
      <Content>
        <Header>
          <RatingWrapper>
            <StyledRating rating={reviewRating} />
            <Author rel='author'>{author}</Author>
            <Time>{formattedDate}</Time>
          </RatingWrapper>
          <Title>{reviewTitle}</Title>
        </Header>
        <MessageContainer>
          <Message>{reviewMessage}</Message>
          <StyledButton onClick={closeModal}>Shop now</StyledButton>
        </MessageContainer>
        <Footer>
          <StyledTextButton
            disabled={activeModal === 0}
            onClick={() => onPrevClick()}
          >
            Previous
          </StyledTextButton>
          <StyledTextButton
            disabled={activeModal === reviewsLength - 1}
            onClick={() => onNextClick()}
          >
            Next
          </StyledTextButton>
        </Footer>
      </Content>
    </StyledReviewModal>
  )
}

export default ReviewModal
