import React, { useState } from 'react'
import styled from 'styled-components'

import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Carousel from '../../../common/Carousel'
import { desktopVw } from '../../../../styles/utils'
import ScaledImage from '../../../ScaledImage'
import { InstagramHandle } from '../../../common/InstagramHandle'
import UGCModal from '../../../UGCImageModal'

const photoPositionConfigMap = {
  '1-4-1': [1, 4, 1],
  '1-1-1': [1, 1, 1],
}

const defaultPhotoPositionCapacity = [1, 4, 1]

const settings = {
  slidesToShow: 3,
  slidesToScroll: 3,
  leftArrowStyle: { left: '-32px' },
}

const Container = styled.div`
  background: ${props => props.theme.colors.lightPink};
  padding: ${desktopVw(125)} ${desktopVw(43)};
  padding-top: ${desktopVw(60)};
`
const Title = styled.div`
  text-align: center;
  padding-bottom: ${desktopVw(30)};
  p {
    font-family: ${props => props.theme.fonts.heading};
    font-weight: 500;
    font-size: ${desktopVw(30)};
    line-height: ${desktopVw(80)};

    i {
      font-family: ${props => props.theme.fonts.secondary};
      font-style: normal;
      letter-spacing: 0em;
      font-weight: 600;
      font-size: ${desktopVw(30)};
      line-height: ${desktopVw(80)};
    }
  }
`

const StyledPhoto = styled(ScaledImage)`
  opacity: 1;
  transition: 0.5s ease;
  width: var(--width);
  height: var(--height);
`

const StyledInstagramHandle = styled(InstagramHandle)`
  opacity: 0;
`

const Wrapper = styled.div`
  display: grid !important;
  grid-template-columns: repeat(2, 1fr);
  row-gap: ${desktopVw(13)};
`

const PhotoContainer = styled.div`
  position: relative;
  width: var(--width);
  cursor: ${({ hasIgHandle }) => (hasIgHandle ? 'pointer' : 'initial')};
  &:hover ${StyledPhoto} {
    opacity: ${({ hasIgHandle }) => (hasIgHandle ? 0.5 : 1)};
  }

  &:hover ${StyledInstagramHandle} {
    opacity: 1;
  }
`

const StyledCarousel = styled(Carousel)``

function getOrderedPhotos(imageList, photoPositionCapacity) {
  const orderedPhotos = []

  let currentPosition = 0

  imageList.forEach(imageObj => {
    if (!orderedPhotos[currentPosition]) orderedPhotos.push([])

    const noOfPhotos =
      photoPositionCapacity[currentPosition % photoPositionCapacity.length]

    if (noOfPhotos === 1) {
      orderedPhotos[currentPosition].push({
        photo: imageObj.imageDesktop,
        isLarge: true,
        instagramHandle: imageObj.instagramHandle,
        id: imageObj.id,
        instagramText: imageObj.instagramText,
        linkedProducts: imageObj.linkedProducts,
        instagramPostLink: imageObj.instagramPostLink,
        instagramPostDate: imageObj.instagramPostDate,
        linkedProductColors: imageObj.linkedProductColors,
      })
    } else
      orderedPhotos[currentPosition].push({
        photo: imageObj.imageDesktop,
        instagramHandle: imageObj.instagramHandle,
        id: imageObj.id,
        instagramText: imageObj.instagramText,
        linkedProducts: imageObj.linkedProducts,
        instagramPostLink: imageObj.instagramPostLink,
        instagramPostDate: imageObj.instagramPostDate,
        linkedProductColors: imageObj.linkedProductColors,
      })

    if (orderedPhotos[currentPosition].length === noOfPhotos) {
      currentPosition += 1
    }
  })

  return orderedPhotos
}

const PhotosListWithTextDesktop = ({
  imageList,
  title,
  socialPhotosConfigDesktop,
}) => {
  const [modalOpen, setModalOpen] = useState('')

  const photoPositionCapacity =
    photoPositionConfigMap[socialPhotosConfigDesktop] ||
    defaultPhotoPositionCapacity

  const orderedPhotos = getOrderedPhotos(imageList, photoPositionCapacity)

  const totalPosts = imageList.length

  const onPrevClick = id => {
    const index = imageList.findIndex(item => item.id === id)
    if (index > 0) {
      setModalOpen(imageList[index - 1].id)
    }
  }

  const onNextClick = id => {
    const index = imageList.findIndex(item => item.id === id)
    if (index < totalPosts - 1) {
      setModalOpen(imageList[index + 1].id)
    }
  }

  return (
    <Container>
      <Title>{documentToReactComponents(title.json)}</Title>
      <StyledCarousel {...settings}>
        {orderedPhotos.map((photoArr, i) => {
          return (
            <Wrapper>
              {photoArr.map(
                ({
                  instagramHandle,
                  isLarge,
                  photo,
                  id,
                  linkedProducts,
                  instagramText,
                  instagramPostDate,
                  instagramPostLink,
                  linkedProductColors,
                }) => (
                  <>
                    <PhotoContainer
                      hasIgHandle={!!instagramHandle}
                      onClick={() => {
                        setModalOpen(id)
                      }}
                      style={{
                        '--width': isLarge ? desktopVw(439) : desktopVw(211),
                      }}
                    >
                      <StyledPhoto
                        fluid={photo.fluid}
                        style={{
                          '--width': isLarge ? desktopVw(439) : desktopVw(211),
                          '--height': isLarge ? desktopVw(425) : desktopVw(206),
                        }}
                      />
                      {instagramHandle && (
                        <StyledInstagramHandle
                          instagramHandle={instagramHandle}
                          overrideAnchor
                        />
                      )}
                    </PhotoContainer>
                    {linkedProducts && (
                      <UGCModal
                        open={modalOpen === id}
                        totalPosts={totalPosts}
                        closeModal={() => setModalOpen(null)}
                        activeModal={i}
                        onPrevClick={() => onPrevClick(id)}
                        onNextClick={() => onNextClick(id)}
                        linkedProducts={linkedProducts}
                        text={instagramText}
                        instagramHandle={instagramHandle}
                        photo={photo}
                        instagramPostDate={instagramPostDate}
                        instagramPostLink={instagramPostLink}
                        linkedProductColors={linkedProductColors}
                        firstProduct={id === imageList[0].id}
                        lastProduct={id === imageList[imageList.length - 1].id}
                      />
                    )}
                  </>
                ),
              )}
            </Wrapper>
          )
        })}
      </StyledCarousel>
    </Container>
  )
}

export default PhotosListWithTextDesktop
