import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import StarIcon from '../assets/star.svg'

const StyledRating = styled.div``

const RatingStar = styled(StarIcon)`
  width: 10px;
  margin-right: 2px;

  .star__inner {
    fill: ${props => props.fill && props.theme.colors.primary};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(10)};
    margin-right: ${desktopVw(2)};
  }
`

const Rating = ({ className, rating }) => {
  return (
    <StyledRating className={className}>
      {[...Array(5)].map((_, i) => (
        <RatingStar
          key={i}
          fill={i < Math.round(rating) ? 1 : 0}
          className='rating__star'
        />
      ))}
    </StyledRating>
  )
}

export default Rating
