import React from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { INLINES, BLOCKS } from '@contentful/rich-text-types'
import { desktopVw, mobileVw } from '../styles/utils'

import Modal from './Modal'
import CloseBtnIcon from '../assets/close-button.svg'
import ScaledImage from './ScaledImage'
import AddProduct from './QuickProductAdd'
import Instagram from './icons/insta_icon'
import Facebook from './icons/fb_icon'
import Email from './icons/Email'
import Caret from '../assets/next_caret.svg'
import { CarouselWrapper } from './common/Carousel/CarouselWrapper'

const desktopSettings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  leftArrowStyle: {
    top: '50%',
    width: '8px',
    height: '14px',
    left: '6px',
    zIndex: 2,
  },
  rightArrowStyle: {
    top: '50%',
    width: '8px',
    height: '14px',
    right: '6px',
    zIndex: 2,
  },
  showDisabledArrows: false,
}

const mobileSettings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  showArrows: false,
  showDots: true,
}

const StyledCarousel = styled(CarouselWrapper)`
  .slick-dots {
    bottom: auto;
  }
`

const QuickProductAdd = styled(AddProduct)`
  &.Quick_add_product-container {
    padding: 26px 8px;
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      padding: 25px;
    }
  }
`

const StyledUGCModal = styled(Modal)`
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: scroll;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    height: auto;
    width: auto;
  }
`

const Content = styled.div`
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  padding: 29px 10px 20px;
  box-sizing: border-box;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: auto;
    width: ${desktopVw(492)};
    padding: ${desktopVw(64)} ${desktopVw(30)};
  }
`

const MessageContainer = styled.div`
  /* flex: 1; */
  // margin-bottom: 37px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    // margin-bottom: ${desktopVw(24)};
  }
`

const PhotoContainer = styled.div`
  flex: 1;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    // margin-bottom: ${desktopVw(24)};
  }
`
const Image = styled(ScaledImage)`
  width: 100%;
  height: auto;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(548)};
    margin: ${desktopVw(21)} 0 ${desktopVw(21)} ${desktopVw(21)};
  }
`

const Message = styled.p`
  display: -webkit-box;

  overflow: hidden;
  width: 100%;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 24px;
  margin-top: 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
    line-height: ${desktopVw(20)};
    margin-bottom: ${desktopVw(24)};
    margin-top: ${desktopVw(59)};
  }
`
const Footer = styled.footer`
  display: flex;
  justify-content: space-between;
  margin-top: 45px;
  align-items: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(26)};
  }
`

const StyledTextButton = styled.div`
  font-family: ${props => props.theme.fonts.heading};
  text-transform: uppercase;
  font-weight: 500;
  height: 100%;
  display: inline-block;
  & > svg {
    stroke: ${props => (props.disabled ? '#BDBDBD' : '#000')};
    cursor: ${props => (props.disabled ? '' : 'pointer')};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const CloseBtn = styled.button`
  position: absolute;
  top: 26px;
  right: 10px;
  z-index: 2;
  cursor: pointer;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(28)};
    right: ${desktopVw(30)};
  }
`

const SocialLinks = styled.div`
  display: flex;
  align-items: center;
  > svg {
    margin-right: ${mobileVw(17)};
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      margin-right: ${desktopVw(11)};
      cursor: pointer;
      height: 15px;
    }
  }
`

const NavLinks = styled.div``

const NextButton = styled(Caret)`
  margin-right: 10px;
  margin-left: 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: 25px;
  }
`
const PrevButton = styled(Caret)`
  margin-right: 20px;
  transform: rotate(180deg);
`

const InstaDesc = styled.div`
  display: flex;
  color: #8f8f8f;
  line-height: 18px;
  font-weight: 400;
  font-size: 11px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`
const InlineLink = styled.a``

const InstaHandle = styled.div`
  cursor: pointer;
`

const InstagramText = styled.div``

const OpenLink = url => {
  window.open(url, '_blank')
}

const UGCModal = ({
  open,
  closeModal,
  firstProduct,
  lastProduct,
  text,
  onNextClick,
  onPrevClick,
  instagramHandle,
  linkedProducts,
  photo,
  instagramPostDate,
  instagramPostLink,
  linkedProductColors,
  isMobile,
}) => {
  const formattedDate = dayjs(instagramPostDate).format('MMMM DD YYYY')

  const options = {
    renderNode: {
      [BLOCKS.PARAGRAPH]: (node, children) => {
        return children.map((child, index) => {
          if (typeof child === 'string' && index == children.length - 1) {
            let str = child.trim()
            if (isMobile) {
              str = str.substr(0, 160)
            }
            str = str.substr(0, str.length - 1)
            str = `${str}<u><a href=${instagramPostLink} target='_blank' rel='noopener noreferrer'>...Read more</a></u>`
            return <span dangerouslySetInnerHTML={{ __html: str }} />
          }
          return child
        })
      },
      [INLINES.HYPERLINK]: node => {
        return (
          <u>
            <InlineLink href={node.data.uri} target='_blank'>
              {node.content[0].value}
            </InlineLink>
          </u>
        )
      },
    },
  }

  return (
    <StyledUGCModal open={open} onClose={closeModal} showCloseBtn={false}>
      <CloseBtn aria-label='Close' onClick={closeModal}>
        <CloseBtnIcon />
      </CloseBtn>
      <PhotoContainer>
        <Image fluid={photo.fluid} />
      </PhotoContainer>
      <Content>
        <MessageContainer>
          <StyledCarousel
            mobileSettings={mobileSettings}
            desktopSettings={desktopSettings}
          >
            {linkedProducts.map((linkedProduct, index) => (
              <QuickProductAdd
                key={linkedProduct.id}
                contentfulProduct={linkedProduct}
                startingSize=''
                tracker='ugc_direct_shop_section'
                colorObj={linkedProductColors[index]}
                addToCartTrackerSection='ugc_direct_shop_section'
                className='Quick_add_product-container'
              />
            ))}
          </StyledCarousel>
          <Message>{documentToReactComponents(text?.json, options)}</Message>
          <InstaDesc>
            <InstaHandle
              onClick={() => {
                if (instagramHandle) {
                  OpenLink(`https://www.instagram.com/${instagramHandle}/`)
                }
              }}
            >
              <InstagramText>
                @{instagramHandle}&nbsp;{` || Instagram || `}&nbsp;
                {formattedDate}
              </InstagramText>
            </InstaHandle>
          </InstaDesc>
        </MessageContainer>
        <Footer>
          <SocialLinks>
            <Facebook
              onClick={() => {
                OpenLink('https://www.facebook.com/onequince')
              }}
            />
            <Instagram
              // width='15'
              // height='15'
              onClick={() => {
                if (instagramHandle) {
                  OpenLink(instagramPostLink)
                }
              }}
            />
            <Email onClick={() => OpenLink('mailto:care@onequince.com')} />
          </SocialLinks>
          <NavLinks>
            <StyledTextButton disabled={firstProduct} onClick={onPrevClick}>
              <PrevButton />
            </StyledTextButton>
            <StyledTextButton disabled={lastProduct} onClick={onNextClick}>
              <NextButton />
            </StyledTextButton>
          </NavLinks>
        </Footer>
      </Content>
    </StyledUGCModal>
  )
}

export default UGCModal
