import React from 'react'
import styled from 'styled-components'
import { SocialPhotos } from './SocialPhotoClientWrapper'
import useMounted from '../../../../hooks/useMounted'
import { desktopVw, mobileVw } from '../../../../styles/utils'

const SocialPhotosPlaceHolder = styled.div`
  min-height: ${mobileVw(538)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    min-height: ${desktopVw(534)};
  }
`

export const SocialPhotosSection = props => {
  const { mounted } = useMounted()
  return (
    <SocialPhotosPlaceHolder>
      {mounted && <SocialPhotos {...props} />}
    </SocialPhotosPlaceHolder>
  )
}
