import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import EyeIcon from '../assets/eye.svg'

const StyledEyeIcon = styled(EyeIcon)`
  position: absolute;
  display: none;
  width: 13.6px;
  height: 10.19px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(13.6)};
    height: ${desktopVw(10.9)};
  }
`

const StyledReviewThumbnail = styled.div`
  position: relative;
  z-index: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 50%;
  border: 1px solid ${props => props.theme.colors.greyLighter};
  width: 52px;
  height: 52px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(52)};
    height: ${desktopVw(52)};

    &:hover {
      ${StyledEyeIcon} {
        display: block;
      }
    }
  }
`

const ImgWrapper = styled.figure`
  margin: 0;
  border-radius: 50%;
  display: block;
  width: 46px;
  height: 46px;
  overflow: hidden;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(46)};
    height: ${desktopVw(46)};
  }
`

const Img = styled.img`
  object-fit: cover;
  width: 100%;
  height: 100%;
  background: ${props => props.theme.colors.greyLighter};
`

const ReviewThumbnail = ({
  children,
  className,
  reviewUserPhotos,
  openModal,
}) => {
  const imageUrl = reviewUserPhotos && reviewUserPhotos.split(',')[0]
  const reviewerPhotoUrl = `https://cdn.stamped.io/uploads/photos/${imageUrl}`
  return (
    <>
      <StyledReviewThumbnail
        onClick={() => openModal && openModal()}
        className={className}
      >
        {openModal && <StyledEyeIcon />}
        <ImgWrapper>
          <Img loading='lazy' src={reviewerPhotoUrl} alt={imageUrl} />
        </ImgWrapper>
      </StyledReviewThumbnail>
      {children && React.cloneElement(children, { reviewerPhotoUrl })}
    </>
  )
}

export default ReviewThumbnail
