import React from 'react'
import useDetectDevices from '../../../../hooks/useDetectDevices'

import PhotosListWithTextDesktop from './PhotosListWithTextDesktop'
import PhotosListWithTextMobile from './PhotosListWithTextMobile'

export const SocialPhotos = ({
  headingDesktop,
  headingMobile,
  socialPhotosConfigDesktop,
  imageWithTextList,
}) => {
  const { devices } = useDetectDevices()
  return (
    <>
      {devices.desktop && (
        <PhotosListWithTextDesktop
          imageList={(imageWithTextList || []).filter(
            imgObj => !!imgObj.imageDesktop,
          )}
          title={headingDesktop}
          socialPhotosConfigDesktop={socialPhotosConfigDesktop}
        />
      )}
      {devices.mobile && (
        <PhotosListWithTextMobile
          imageList={(imageWithTextList || []).filter(imgObj => !!imgObj.image)}
          title={headingMobile}
        />
      )}
    </>
  )
}
