import React, { useState } from 'react'
import styled from 'styled-components'

import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import Carousel from '../../../common/Carousel'
import ScaledImage from '../../../ScaledImage'
import { mobileVw } from '../../../../styles/utils'
import { chunk } from '../../../../utils/build'
import { InstagramHandle } from '../../../common/InstagramHandle'
import UGCModal from '../../../UGCImageModal'

const CHUNK_SIZE = 4

const settings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  showArrows: false,
  showDots: true,
}

const Container = styled.div`
  background: ${props => props.theme.colors.lightPink};
  padding: 35px 18px 78px;
`

const Title = styled.div`
  text-align: center;
  padding-bottom: 35px;
  p {
    font-family: ${props => props.theme.fonts.heading};
    font-weight: 500;
    letter-spacing: 0px;
    font-style: normal;
    font-size: ${mobileVw(25)};
    line-height: ${mobileVw(30)};

    i {
      font-family: ${props => props.theme.fonts.secondary};
      font-style: normal;
      letter-spacing: 0em;
      font-weight: 500;
      font-size: ${mobileVw(30)};
      line-height: ${mobileVw(30)};
    }
  }
`

const StyledPhoto = styled(ScaledImage)`
  width: ${mobileVw(167)};
  height: ${mobileVw(167)};
`

const Wrapper = styled.div`
  display: grid !important;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 9px;
  column-gap: 9px;
`

const StyledCarousel = styled(Carousel)`
  .slick-dots {
    bottom: -38px;
  }
`

const PhotoContainer = styled.div`
  text-align: center;
`

const StyledInstagramHandle = styled(InstagramHandle)`
  padding-top: 5px;
  padding-bottom: 5px;
`

const PhotosListWithTextMobile = ({ imageList, title }) => {
  const [modalOpen, setModalOpen] = useState('')

  const photosInChunks = chunk(imageList, CHUNK_SIZE)

  const totalPosts = imageList.length

  const onPrevClick = id => {
    const index = imageList.findIndex(item => item.id === id)
    if (index > 0) {
      setModalOpen(imageList[index - 1].id)
    }
  }

  const onNextClick = id => {
    const index = imageList.findIndex(item => item.id === id)
    if (index < totalPosts - 1) {
      setModalOpen(imageList[index + 1].id)
    }
  }
  return (
    <Container>
      <Title>{documentToReactComponents(title.json)}</Title>
      <StyledCarousel {...settings}>
        {photosInChunks.map(photoArr => {
          return (
            <Wrapper>
              {photoArr.map(
                ({
                  image,
                  instagramHandle,
                  id,
                  linkedProducts,
                  instagramText,
                  instagramPostDate,
                  instagramPostLink,
                  linkedProductColors,
                }) => {
                  return (
                    <>
                      <PhotoContainer
                        onClick={() => {
                          setModalOpen(id)
                        }}
                      >
                        <StyledPhoto fluid={image.fluid} />
                        {instagramHandle && (
                          <StyledInstagramHandle
                            instagramHandle={instagramHandle}
                            overrideAnchor
                          />
                        )}
                      </PhotoContainer>
                      {linkedProducts && (
                        <UGCModal
                          open={modalOpen === id}
                          totalPosts={totalPosts}
                          closeModal={() => setModalOpen(null)}
                          onPrevClick={() => onPrevClick(id)}
                          onNextClick={() => onNextClick(id)}
                          linkedProducts={linkedProducts}
                          text={instagramText}
                          instagramHandle={instagramHandle}
                          photo={image}
                          instagramPostDate={instagramPostDate}
                          instagramPostLink={instagramPostLink}
                          linkedProductColors={linkedProductColors}
                          firstProduct={id === imageList[0].id}
                          lastProduct={
                            id === imageList[imageList.length - 1].id
                          }
                          isMobile
                        />
                      )}
                    </>
                  )
                },
              )}
            </Wrapper>
          )
        })}
      </StyledCarousel>
    </Container>
  )
}

export default PhotosListWithTextMobile
