import React from 'react'

// Hooks
import useShopifyProduct from '../hooks/useShopifyProduct'

// Components
import { withErrorBoundary } from './ErrorBoundary/ErrorBoundary'
import ProductAdd from './AddProductCard'

const AddProductCard = ({
  contentfulProduct,
  tracker,
  colorObj = {},
  addToCartTrackerSection = 'ugc_direct_shop_section',
  className,
}) => {
  const {
    shopifyProduct: { handle },
  } = contentfulProduct

  const { product } = useShopifyProduct(handle)

  return (
    <ProductAdd
      shopifyProduct={product}
      product={contentfulProduct}
      startingColor={colorObj.name}
      startingSize=''
      tracker={tracker}
      addToCartTrackerSection={addToCartTrackerSection}
      className={className}
    />
  )
}

export default withErrorBoundary(AddProductCard, {
  fallbackRender: () => <div />,
})
