import React from 'react'

import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import styled from 'styled-components'
import { desktopVw } from '../styles/utils'
import { getLinkHref } from '../utils'

const SVGDesktop = styled.img`
  display: none !important;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block !important;
    width: ${desktopVw(75)};
    height: ${desktopVw(75)};
  }
`

const SVGMobile = styled.img`
  width: 54px;
  height: 54px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none !important;
  }
`

const TitleDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    margin: ${desktopVw(15)} 0;
    p {
      font-size: ${desktopVw(15)};
      line-height: ${desktopVw(13.62)};
      font-weight: 500;
    }
  }
`

const TitleMobile = styled.div`
  p {
    font-size: 13px;
    line-height: 11.8px;
    font-weight: 500;
  }
  margin: 10px 0;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const TextDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    text-align: center;
    p {
      font-size: ${desktopVw(15)};
      line-height: ${desktopVw(18.87)};
    }
  }
`

const TextMobile = styled.div`
  text-align: center;
  p {
    font-size: 11px;
    line-height: 13.84px;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

function getNormalizedSrc({ svgDesktop, svgMobile }, isMobile) {
  let finalSrc = svgDesktop || svgMobile
  if (isMobile) {
    finalSrc = svgMobile || svgDesktop
  }
  return finalSrc.file.url
}

function getNormalizedText({ textDesktop, textMobile }, isMobile) {
  let finalTextObj = textDesktop || textMobile
  if (!finalTextObj) return null
  if (isMobile) {
    finalTextObj = textMobile || textDesktop
  }
  return finalTextObj.json
}

function getNormalizedTitle({ titleDesktop, titleMobile }, isMobile) {
  let finalTextObj = titleDesktop || titleMobile
  if (!finalTextObj) return null
  if (isMobile) {
    finalTextObj = titleMobile || titleDesktop
  }
  return finalTextObj.json
}

const SVGWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  a {
    text-decoration: underline;
  }
`

const ReadThem = styled.p`
  text-decoration: underline;
  cursor: pointer;
`

export function SVGWithText({
  svgDesktop,
  svgMobile,
  titleDesktop,
  titleMobile,
  textDesktop,
  textMobile,
  svgLink,
  className,
  entryTitle,
  reviewSectionScrollHandler,
  isPrReview,
}) {
  function withSVGLink(renderer) {
    return (
      <>
        {svgLink ? (
          <a href={getLinkHref(svgLink)} target='_blank' rel='noreferrer'>
            {renderer}
          </a>
        ) : (
          renderer
        )}
      </>
    )
  }

  const readThem = () => (
    <ReadThem onClick={reviewSectionScrollHandler} role='presentation'>
      Read them
    </ReadThem>
  )

  return (
    <SVGWrapper className={className}>
      <span>
        {(svgDesktop || svgMobile) &&
          withSVGLink(
            <SVGDesktop
              src={getNormalizedSrc({ svgDesktop, svgMobile })}
              alt={entryTitle}
            />,
          )}
        {(svgDesktop || svgMobile) &&
          withSVGLink(
            <SVGMobile
              src={getNormalizedSrc({ svgDesktop, svgMobile }, true)}
              alt={entryTitle}
            />,
          )}
      </span>
      {(titleDesktop || titleMobile) && (
        <TitleDesktop className='svg__with__text_title_desktop'>
          {documentToReactComponents(
            getNormalizedTitle({ titleDesktop, titleMobile }),
          )}
        </TitleDesktop>
      )}
      {(titleDesktop || titleMobile) && (
        <TitleMobile className='svg__with__text_title_mobile'>
          {documentToReactComponents(
            getNormalizedTitle({ titleDesktop, titleMobile }, true),
          )}
        </TitleMobile>
      )}
      {(textDesktop || textMobile) && (
        <TextDesktop className='svg__with__text_text_desktop'>
          {documentToReactComponents(
            getNormalizedText({ textDesktop, textMobile }),
          )}
          {isPrReview && readThem()}
        </TextDesktop>
      )}
      {(textDesktop || textMobile) && (
        <TextMobile className='svg__with__text_text_mobile'>
          {documentToReactComponents(
            getNormalizedText({ textDesktop, textMobile }, true),
          )}
          {isPrReview && readThem()}
        </TextMobile>
      )}
    </SVGWrapper>
  )
}
