import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { CarouselWrapper } from './common/Carousel/CarouselWrapper'
import { SVGWithText } from './SVGWithText'
import { desktopVw, mobileVw } from '../styles/utils'
import useDetectDevices from '../hooks/useDetectDevices'

const desktopSettings = {
  slidesToShow: 3,
  slidesToScroll: 3,
}

const mobileSettings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  showArrows: false,
  showDots: true,
}

const StyledCarousel = styled(CarouselWrapper)`
  background: ${props => props.backgroundColor ?? props.theme.colors.lightPink};
  padding: ${mobileVw(38)} ${mobileVw(39)} ${mobileVw(80)};
  .slick-dots {
    bottom: ${mobileVw(-46)};
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(70)} ${desktopVw(88)};
  }
`

const StyledSVG = styled(SVGWithText)`
  img {
    width: 229px;
    height: 50px;
    margin-bottom: 20px;
  }
  p {
    font-size: ${mobileVw(14)};
    line-height: ${mobileVw(21)};
  }

  .svg__with__text_svg_mobile {
    width: auto !important;
    height: ${mobileVw(32)} !important;
    margin-bottom: ${mobileVw(25)} !important;
    max-width: ${mobileVw(229)};
  }

  .svg__with__text_title_mobile {
    text-align: center;
    margin-bottom: ${mobileVw(20)};

    p {
      font-size: ${mobileVw(14)};
      line-height: ${mobileVw(21)};
    }
    h1 {
      font-family: ${props => props.theme.fonts.secondary};
      font-weight: 700;
      font-size: ${mobileVw(30)};
      line-height: ${mobileVw(32)};
    }

    h3 {
      font-weight: 400;
      font-family: ${props => props.theme.fonts.primary};
      font-size: ${mobileVw(20)};
      line-height: ${mobileVw(22)};
    }
  }

  .svg__with__text_text_mobile {
    p {
      font-size: ${mobileVw(11)};
      line-height: ${mobileVw(13.84)};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: auto;
    width: ${desktopVw(310)};
    img {
      width: auto !important;
      height: ${desktopVw(32)} !important;
      max-width: ${desktopVw(229)};
      margin-bottom: ${desktopVw(26)};
    }
    p {
      font-size: ${desktopVw(14)};
      line-height: ${desktopVw(21)};
    }

    .svg__with__text_svg_desktop {
      width: auto !important;
      height: ${desktopVw(32)} !important;
      max-width: ${desktopVw(229)};
    }

    .svg__with__text_title_desktop {
      margin-top: 0;
      margin-bottom: 0;
      text-align: center;

      p {
        font-family: ${props => props.theme.fonts.primary};
        font-weight: 500;
      }
      h1 {
        font-family: ${props => props.theme.fonts.secondary};
        font-weight: 700;
        font-size: ${desktopVw(36)};
        line-height: ${desktopVw(37.19)};
      }

      h3 {
        font-weight: 400;
        font-family: ${props => props.theme.fonts.primary};
        font-size: ${desktopVw(18)};
        line-height: ${desktopVw(22.64)};
      }
    }

    .svg__with__text_text_desktop {
      margin-top: ${desktopVw(24)};
    }
  }
`

const PRPanelReviewsTitle = 'PR Panel - Reviews'
const numberOfPrPanelsPerTime = 3
const positionInPanel = 2

export const PrPanel = ({
  panelList,
  className,
  style,
  reviewSectionScrollHandler,
  showReviewsCount = false,
  backgroundColor,
}) => {
  const listWithoutPrPanelReviews = panelList.filter((pr, prIdx) => prIdx !== 1)
  const prPanelReview = panelList[1]

  const [panelListForRender, setPanelListForRender] = useState(panelList)

  const getPrPanelListForDesktop = () => {
    const list = []

    // eslint-disable-next-line immutable/no-let
    let panelListIndex = 0
    // eslint-disable-next-line immutable/no-let
    let currentIdx = 0

    while (panelListIndex < listWithoutPrPanelReviews.length) {
      const isReviewsIndex =
        (currentIdx - positionInPanel + 1) % numberOfPrPanelsPerTime === 0

      if (isReviewsIndex) {
        list.push(prPanelReview)
      } else {
        list.push(listWithoutPrPanelReviews[panelListIndex])
        panelListIndex += 1
      }

      currentIdx += 1
    }

    return list
  }

  const { devices } = useDetectDevices()

  useEffect(() => {
    if (!showReviewsCount) {
      setPanelListForRender(listWithoutPrPanelReviews)
    } else if (prPanelReview && devices.desktop) {
      setPanelListForRender(getPrPanelListForDesktop())
    } else if (devices.mobile) {
      setPanelListForRender(panelList)
    }
  }, [devices])

  return (
    <div className={className} style={style}>
      <StyledCarousel
        mobileSettings={mobileSettings}
        desktopSettings={desktopSettings}
        backgroundColor={backgroundColor}
      >
        {panelListForRender.map((panel, panelIdx) => {
          if (showReviewsCount) {
            return (
              <StyledSVG
                {...panel}
                reviewSectionScrollHandler={reviewSectionScrollHandler}
                reviewSectionTitle={PRPanelReviewsTitle}
                isPrReview={
                  devices.desktop ? (panelIdx - 1) % 3 === 0 : panelIdx === 1
                }
              />
            )
          }

          return <StyledSVG {...panel} />
        })}
      </StyledCarousel>
    </div>
  )
}
